<template>
  <section class="proyectos">
    <section class="proyecto-textos">
      <h1>{{ proyecto.nombre }}</h1>
      <p class="proyecto-descripcion">{{ proyecto.descripcion }}</p>
      <div>
        <a
          :href="proyecto.link2"
          target="blank"
          v-show="showLink(proyecto.link2)"
        >
          <svg-icon class="icono" type="mdi" :size="25" :path="pathLink" />
        </a>
        <a
          :href="proyecto.linkgit"
          target="blank"
          v-show="showLink(proyecto.linkgit)"
        >
          <svg-icon class="icono" type="mdi" :size="25" :path="pathGit" />
        </a>
      </div>
    </section>
    <img
      class="imagen"
      :src="require(`@/assets/proyectos/${proyecto.img1}`)"
      :alt="proyecto.nombre"
      :style="{ maxWidth: proyecto.imgSize + '%' }"
    />

  </section>
</template>

<script>
import store from "../basedatos/trabajos.js";
import SvgIcon from "@jamescoyle/vue-icon";
import { mdiGithub, mdiOpenInNew } from "@mdi/js";
export default {
  data() {
    return {
      pathGit: mdiGithub,
      pathLink: mdiOpenInNew,
    };
  },
  components: {
    SvgIcon,
  },
  props: {
    proyectoSlug: {
      type: String,
      required: true,
    },
  },
  computed: {
    proyecto() {
      return store.proyectos.find(
        (proyecto) => proyecto.slug === this.proyectoSlug
      );
    },
  },
  methods: {
    showLink(input) {
      return input == "" ? false : true;
    },
  },
};
</script>

